<template>
  <div id="app">
    <main class="app__layout">
      <header class="header">
        <div class="header__left">
        <button class="button" @click="$router.go(-1)">
          <font-awesome-icon icon="arrow-left" />
        </button>
        <div class="brand">Whats On</div>
        </div>
        <div class="header__center">
          <input v-model="searchQuery" class="search__input" type="search" placeholder="Search">
          <button @click="runSearch">
            <font-awesome-icon icon="search" />
          </button>
        </div>
        <div class="header__right">
          <!-- <button>
            <font-awesome-icon icon="film" />
          </button>
          <button>
            <font-awesome-icon icon="tv" />
          </button>` -->
        </div>
      </header>
      <transition name="fade" appear mode="out-in">
        <router-view></router-view>
      </transition>
    </main>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      searchQuery: null,
    };
  },
  methods: {
    runSearch() {
      this.$store.dispatch('search', [this.searchQuery]);
      this.$router.push('/search');
    },
  },
};
</script>

<style lang="scss"> 
* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  color: #fff;
  background-color: #222;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.header {
  position: fixed;
  height: 100%;
  max-height: 64px;
  width: 100%;
  background-color: #222;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  box-shadow: 0 0 6px 1px rgba(0,0,0,0.45);
  z-index: 100;
  &__left {
    display: flex;
    .brand {
      font-size: 1.4rem;
      font-weight: bold;
      padding-left: 1rem;
    }
  }
  &__center {
    flex-grow: 1;
    display: inline-flex;
    justify-content: center;
  }
}
.search__input {
  display: block;
  padding: 0.5rem;
  background-color: #333;
  border: 1px solid #444;
  border-radius: 0.25rem;
  font-size: 1.2rem;
  color: white;
  width: 100%;
  max-width: 320px;
}

h1 {
  font-size: 2rem;
  line-height: 2.2rem;
  margin: 0;
  margin-bottom: 1rem;
}

button {
  background-color: transparent;
  border: 0;
  color: white;
  background-color: #c62828;
  font-size: 1.2rem
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



