import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    search: {
      results: [],
      query: null,
    },
    movies: [],
  },
  mutations: {
    pushSearchResults(state, payload) {
      Vue.set(state.search, 'results', payload);
    },
    pushMovies(state, payload) {
      Vue.set(state, 'movies', payload);
    },
  },
  actions: {
    search(context, [query]) {
      axios.get(`https://api.themoviedb.org/3/search/movie?query=${query}&api_key=a99cb79115ec65cdb37247ef52887471`)
        .then((res) => { context.commit('pushSearchResults', res.data.results); });
    },
    getMovies(context) {
      axios.get('https://api.themoviedb.org/3/discover/movie?api_key=a99cb79115ec65cdb37247ef52887471')
        .then((res) => { context.commit('pushMovies', res.data.results); });
    },
  },
});

export default store;



